@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  box-sizing: border-box;
}

html, body{
  margin: 0;
  padding: 0;
}

body{
  background-color: #803ced;;
}

body, input, button{
  font-family: 'Noto Sans', sans-serif;
}

label, input, button{
  font-weight: 700;
  border-radius: 6px;
}

button{
  background-color: transparent;
  border: none;
}

a, button{
  transition: all 0.2s linear;
}


.all{
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}


.menu{
  padding: 30px;
  background-color: #642FBA;
}
.menu ul{
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu ul li{
  display: contents;
}
.menu ul li a, .menu ul li button{
  padding: 5px 10px;
  cursor: pointer;
  color: #FFF;
  font-size: 180%;
  font-weight: 700;
}
.menu ul li a:hover, .menu ul li button:hover{
  padding-bottom: 5px;
  color: #fc894d;
}


.cuerpo{
  padding: 40px;
  color: #FFF;
}


.wrap{
  margin: 0 auto;
  padding: 40px;
  max-width: 920px;
  color: #555;
  background-color: #DDD;
  border-radius: 6px;
}


@media all and (max-width: 920px){
  .wrap{
    margin-left: 20px;
    margin-right: 20px;
  }

  nav, .menu ul, .menu ul li{
    display: -webkit-flex;
    display: flex;
    width: 100%;
  }

  .menu ul{
    display: grid;
  }

  .menu ul li{
    -webkit-justify-content: center;
            justify-content: center;
    border-bottom: 1px solid #666;
  }

  .menu ul li a{
    font-size: 140%;
  }

  .menu ul li:last-child{
    border: none;
  }
}
.form{
  display: grid;
  grid-gap: 30px 20px;
  grid-template-columns: calc(50% - 10px) calc(50% - 10px);
}

.form div:last-child{
  grid-column-end: span 2;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.form label, form input{
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.form input{
  margin: 5px 0;
  padding: 10px;
  color: #555;
  background-color: #EEE;
  border: none;
}

.form div.error{
  color: red;
  font-size: 80%;
}

.form button{
  padding: 10px 30px;
  cursor: pointer;
  color: #FFF;
  font-size: 100%;
  background-color: #803ced;;
  border: none;
}
.form button:hover{
  background-color: #642FBA;
}

.form button.esconde{
  display: none;
}


@media all and (max-width: 720px){
  .form{
    grid-template-columns: 1fr;
  }

  .form div:last-child{
    grid-column-end: auto;
  }  
}
.modal{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  display: -webkit-flex;
  display: flex;
  width: 100%;
  height: 0;
  overflow: hidden;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 160%;
  font-weight: 700;
  color: #FFF;
  background-color: rgba(0,0,0,0.9);
  opacity: 0;
  transition: all 300ms ease;
}

.modal.show{
  height: 100%;
  opacity: 1;
}
